$primaryColor: #333945;
$secondColor: #941759;
$bgColor: linear-gradient(135deg, #a0ca3f 0%, #7b9c30 100%);
$fontTitle: "Rockwell";
$fontText: "Montserrat";
$animTime: 500ms;

@mixin font-face($name, $file, $weight: 400, $style: normal) {
  @font-face {
    font-family: "#{$name}";
    src: local("#{$file}"), url("../fonts/#{$file}.woff2") format("woff2"),
      url("../fonts/#{$file}.woff") format("woff");
    font-weight: $weight;
    font-style: $style;
    font-display: swap;
  }
}

@mixin mon-medium {
  font-family: Montserrat-Medium, sans-serif;
  font-weight: 500;
}

@mixin mon-semi {
  font-family: Montserrat-Semibold, sans-serif;
  font-weight: 600;
}
@mixin mon-bold {
  font-family: Montserrat-Bold, sans-serif;
  font-weight: 700;
}
@mixin mon-regular {
  font-family: Montserrat-Regular, sans-serif;
  font-weight: 400;
}

@mixin roboto-regular {
  font-family: Roboto-Regular, sans-serif;
  font-weight: 400;
}

@mixin roboto-medium {
  font-family: Roboto-Medium, sans-serif;
  font-weight: 500;
}

@mixin rock-bold {
  font-family: rockb, sans-serif;
  font-weight: 700;
}
@mixin r($width) {
  @media only screen and (max-width: $width+ "px") {
    @content;
  }
}

@mixin rmin($width) {
  @media only screen and (min-width: $width+ "px") {
    @content;
  }
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

$t-main: #333945;
$title: #555a64;
$header-title: #ffffff;
$btn-bg: #941759;
$bg-main: #f2fbdd;
$login: #81a332;
$droptext: #79808d;
$drophover: #f9f9f9;

$r-sm: 576;
$r-md: 768;
$r-lg: 992;
$r-xl: 1200;

@import "./func";

* {
  margin: 0;
  box-sizing: border-box;
  object-fit: contain;
  outline: none;
  &:focus {
    background: none;
    outline: none;
  }
}

a {
  text-decoration: none;
  color: $primaryColor;
  font-family: $fontText;
  font-weight: 500;
}

section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.container-verySmall {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 833px;
  width: 100%;
}

.container-small {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 984px;
  width: 100%;
}

.container-middle {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1222px;
  width: 100%;
}

.container-large {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1400px;
  width: 100%;
}

.btn {
  cursor: pointer;
}

.btns {
  display: flex;
  align-items: center;
  gap: 0;

  a {
    &:first-child {
      margin-right: 24px;
      @media (max-width: 440px) {
        margin-right: 14px;
      }
    }
  }
}

.btn {
  font-family: $fontText;
  font-size: 14px;
  @include adap-font(14, 14);
  font-weight: 600;
  padding: 14px 24px;
  border-radius: 4px;
  white-space: nowrap;

  @include less(mobileL) {
    padding: 16px 26px;
  }
  @media (max-width: 440px) {
    padding: 13px 15px;
  }
}

.title {
  padding-bottom: 16px;
  font-family: $fontTitle;
  font-weight: 700;
  @include adap-font(36, 26);
  letter-spacing: 1px;
  color: $primaryColor;
  text-align: center;
}

.subTitle {
  font-family: $fontText;
  font-weight: 500;
  @include adap-font(24, 18);
  color: $primaryColor;
  text-align: center;
}

@import "header";
@import "./watch";
@import "./solution";
@import "./automate";
@import "./feature";
@import "./slider";
@import "./marketplace";
@import "./plan";
@import "./faq";
@import "./community";
@import "./blog";
@import "./footer";
