.watch {
  padding: 0 20px;
  padding-top: 24px;
  padding-bottom: 87px;
  background-color: #f2fbdd;
  @include less(tabletL) {
    padding-bottom: 30px;
  }
  @include less(mobileL) {
    padding-top: 0;
  }
}

.video {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 32px;

  .play-bg {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 135px;
    width: 135px;
    border-radius: 70px;
    filter: drop-shadow(0px 0px 30px rgba(185, 201, 94, 0.5));
    background: #5d682e;
    box-shadow: rgba(185, 201, 94, 0.15) 0px 0px 20px 10px;
    z-index: 2;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transition: visibility $animTime ease-in-out, opacity $animTime ease-in-out,
      box-shadow $animTime ease-in-out, background-color $animTime ease-in-out;
    @media (max-width: 600px) {
      @include pref(transform, scale(0.8));
    }
    @media (max-width: 450px) {
      @include pref(transform, scale(0.6));
    }
    &:hover {
      box-shadow: rgba(185, 201, 94, 0.15) 0px 0px 20px 20px;
      background: rgba(185, 201, 94, 0.15);
      .play-first-bg {
        background: #abd83a;
      }
    }

    .play-first-bg {
      position: absolute;
      height: 100px;
      width: 100px;
      border-radius: 75px;
      background: #8fb538;
      z-index: 2;
      transition: $animTime background-color ease-in-out;
    }
    .play {
      position: absolute;
      top: 51%;
      left: 54%;
      transform: translate(-50%, -50%);
    }
  }

  .play-bg.active {
    opacity: 1;
    visibility: visible;
  }

  .video-bg {
    position: absolute;
    height: 552px;
    width: 984px;
    object-fit: cover;
    border-radius: 8px;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transition: visibility $animTime ease-in-out, opacity $animTime ease-in-out;
    @include less(tabletL) {
      height: 400px;
      width: 600px;
    }
    @media (max-width: 700px) {
      height: 300px;
      width: 500px;
    }
    @media (max-width: 600px) {
      height: 250px;
      width: 400px;
    }
    @media (max-width: 500px) {
      height: 230px;
    }
    @media (max-width: 450px) {
      height: 212px;
      width: 377px;
    }
    @media (max-width: 400px) {
      height: 180px;
      width: 330px;
    }
    @media (max-width: 360px) {
      height: 160px;
      width: 280px;
    }
  }

  .video-bg.active {
    opacity: 1;
    visibility: visible;
  }

  iframe {
    height: 552px;
    width: 984px;
    border-radius: 8px;
    box-shadow: 0 39px 20px #00000017;
    border-radius: 8px;
    cursor: pointer;
    @include less(tabletL) {
      height: 400px;
      width: 600px;
    }
    @media (max-width: 700px) {
      height: 300px;
      width: 500px;
    }
    @media (max-width: 600px) {
      height: 250px;
      width: 400px;
    }
    @media (max-width: 500px) {
      height: 230px;
    }
    @media (max-width: 450px) {
      height: 212px;
      width: 377px;
    }
    @media (max-width: 400px) {
      height: 180px;
      width: 330px;
    }
    @media (max-width: 360px) {
      height: 160px;
      width: 280px;
    }
  }
}
