.community {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  padding-top: 64px;
  padding-bottom: 88px;
  .btn {
    max-width: 213px;
    color: white;
    background: $secondColor;
    transition: color $animTime ease-in-out,
      background-color $animTime ease-in-out;
    &:hover {
      background-color: #691341;
    }
  }
}

.community-content {
  display: flex;
  padding-top: 50px;
  padding-bottom: 40px;
  @include less(tabletL) {
    flex-direction: column;
    align-items: center;
  }
  .items {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 180px;
    width: 100%;
    margin-right: 80px;
    cursor: default;
    &:first-child {
      transition: $animTime transform ease-in-out;

      svg {
        path {
          transition: $animTime fill ease-in-out;
        }
      }
      @include more(tabletL) {
        &:hover {
          @include pref(transform, scale(1.1));

          svg {
            path {
              fill: #941759;
            }
          }
        }
      }
    }
    &:nth-child(2) {
      transition: $animTime transform ease-in-out;

      svg {
        path {
          transition: $animTime stroke ease-in-out;
        }
      }
      @include more(tabletL) {
        &:hover {
          @include pref(transform, scale(1.1));

          svg {
            path {
              stroke: #941759;
            }
          }
        }
      }
    }

    &:last-child {
      margin-right: 0;
      transition: $animTime transform ease-in-out;
      svg {
        path {
          transition: $animTime fill ease-in-out;
        }
      }

      p {
        padding-left: 10px;
      }
      @include more(tabletL) {
        &:hover {
          @include pref(transform, scale(1.1));

          svg {
            path {
              fill: #941759;
            }
          }
        }
      }
    }
    @include less(tabletL) {
      margin-right: 0;
      margin-top: 20px;
    }
    text-align: center;
    font-family: $fontText;
    font-weight: 500;

    svg,
    img {
      height: 60px;
      width: 60px;
    }

    p {
      padding-top: 30px;
    }
  }
}
