.feature {
  padding-top: 64px;
  padding-bottom: 88px;
  overflow: hidden;
  @include less(mobileL) {
    padding-bottom: 48px;
  }

  h1 {
    max-width: 800px;
    margin: 0 auto;
  }
}

.subTitle {
  @include less(tabletL) {
    padding: 0 20px;
  }
}

.feature-content {
  position: relative;
  display: grid;
  grid-template-columns: 275px 275px 275px;
  justify-content: center;
  gap: 56px;
  row-gap: 40px;
  padding-top: 32px;
  @include less(tabletL) {
    grid-template-columns: 275px 275px;
  }
  @media (max-width: 630px) {
    grid-template-columns: 245px 245px;
    gap: 30px;
    row-gap: 20px;
  }
  @media (max-width: 570px) {
    grid-template-columns: 205px 205px;
    gap: 10px;
    row-gap: 10px;
  }
  @include less(mobileL) {
    grid-template-columns: 193px 193px;
  }
  @media (max-width: 430px) {
    grid-template-columns: 170px 170px;
  }
  @media (max-width: 380px) {
    grid-template-columns: 140px 140px;
  }
  .items {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 16px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0px 62px 80px rgba(0, 0, 0, 0.07);
    z-index: 3;
    img {
      max-width: 32px;
      width: 100%;
    }
    svg {
      height: 50px;
      max-width: 50px;
    }
  }
  h3 {
    font-family: $fontTitle;
    @include adap-font(20, 20);

    font-weight: 400;
    padding-top: 16px;
    color: $primaryColor;
    text-align: center;
  }
  .text {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding-top: 18px;

    p {
      width: 100%;
      color: $primaryColor;
      @include adap-font(12, 12);
      font-family: $fontText;
      line-height: 18px;
      @include less(mobileL) {
        line-height: 15px;
      }
    }
    .link {
      //display: flex;
      display: none;
      justify-content: flex-end;
      width: 100%;
      padding-top: 10px;
    }
    a {
      display: flex;
      align-items: center;
      color: $secondColor;
      font-size: 12px;
      transition: $animTime color ease-in-out;

      &:hover {
        color: #68103f;
        .arrow {
          border-color: #68103f;
        }
      }

      .arrow {
        height: 6px;
        width: 6px;
        margin-left: 17px;
        @include pref(transform, rotate(45deg));
        border-color: $secondColor;
        border-style: solid;
        border-radius: 2px;
        border-width: 2px 2px 0 0;
        transition: $animTime border-color ease-in-out;
      }
    }
  }
  .bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
