@include less(tabletL) {
  min-height: 1240px;
}
@include less(tablet) {
  min-height: 1030px;
}

@include less(mobileL) {
  min-height: 955px;
}

.header-bottom-decor {
  @include less(tabletL) {
    clip-path: none;
    left: auto;
    right: -101%;
    bottom: -50%;
    height: 760px;
    width: 229%;
    border-radius: 50%;
  }
  @include less(tablet) {
    height: 600px;
    width: 236%;
  }
  @media (max-width: 675px) {
    width: 242%;
  }
  @media (max-width: 580px) {
    width: 237%;
  }
  @include less(mobileL) {
    height: 720px;
    transform: rotateX(45deg);
    border-radius: 48%;
  }
}

.header-bottom {
  @include less(tabletL) {
    flex-direction: column;
    align-items: center;

    .title {
      text-align: center;
    }

    .btns,
    .header-bottom-left-msg {
      display: none;
    }
  }

  .btns-mobile {
    display: none;
    @include less(tabletL) {
      display: flex;
      margin-top: 0px;
    }
    @include less(mobileL) {
      justify-content: flex-end;
      width: 100%;
    }
    a {
      margin-right: 0;
    }
  }

  .header-bottom-left-msg-mobile {
    display: none;
    @include less(tabletL) {
      display: block;
      margin-top: 20px;
      margin-left: 145px;
      color: $primaryColor;
      font-family: $fontText;
      font-size: 14px;
    }
    // @include less(mobileL) {
    //   margin-left: 54px;
    // }
  }
}
