$maxWidth: 1600;
@mixin adap-font($pcSize, $mobSize) {
  $addSize: $pcSize - $mobSize;
  $maxWidth: $maxWidth - 414;
  font-size: calc(
    #{$mobSize + px} + #{$addSize} * ((100vw - 375px) / #{$maxWidth})
  );
}

@mixin pref($key, $value) {
  #{-webkit- + $key}: $value;
  #{-ms- + $key}: $value;
  #{-moz- + $key}: $value;
  #{-o- + $key}: $value;
  #{$key}: $value;
}

// CONTAINERS
$k2: 2560px;
$desktop: 1920px;
$laptopXL: 1680px;
$laptopL: 1640px;
$laptopM: 1440px;
$laptopS: 1280px;
$tabletL: 1024px;
$tablet: 768px;
$tabletS: 600px;
$mobileL: 480px;
$mobileM: 375px;
$mobileS: 320px;

// target - is your pixels
// device - is on which device it shoud look like
@function get-vw($target, $device) {
  $vw-context: ($device * 0.01);
  @return ($target/$vw-context) * 1vw;
}

@mixin more($size) {
  @if $size == k2 {
    @media (min-width: $k2) {
      @content;
    }
  } @else if $size == desktop {
    @media (min-width: $desktop) {
      @content;
