.blog {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  padding-top: 64px;
  padding-bottom: 40px;
  background: #f2fbdd;
  a {
    padding-top: 32px;
    color: $secondColor;
    @include adap-font(14, 12);
    text-decoration: underline;
    transition: $animTime color ease-in-out;

    &:visited {
      color: #bb6492;
    }
    &:hover {
      color: #68103f;
    }
  }

  .blog-content {
    display: flex;
    padding-top: 54px;
    @include less(tabletL) {
      flex-direction: column;
      align-items: center;
    }
    .items {
      max-width: 350px;
      width: 100%;
      margin-top: 20px;
      margin-right: 31px;
      background: white;

      @include less(laptopS) {
        max-width: 280px;
      }
      @include less(tabletL) {
        margin-right: 0;
      }

      img {
        max-width: 354px;
        width: 100%;
      }
      .text {
        padding: 24px;
        h3 {
          font-family: $fontTitle;
          font-weight: 400;
          font-size: 24px;
          color: $primaryColor;
        }
        p {
          height: 77px;
          font-family: $fontText;
          font-size: 12px;
          color: $primaryColor;
          padding: 16px 0;
        }

        a {
          padding-top: 20px;
        }
      }
    }
  }
}
