section.first {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 730px;
  padding-top: 40px;
  overflow: hidden;
  background: linear-gradient(135deg, #a0ca3f 0%, #7b9c30 100%);

  @media (max-width: 1550px) {
    padding: 0 140px;
    padding-top: 40px;
  }

  @media (max-width: 1360px) {
    padding: 0 100px;
    padding-top: 40px;
  }

  @include less(laptopS) {
    padding: 0 70px;
    padding-top: 20px;
  }

  @media (max-width: 1170px) {
    min-height: 660px;
    padding: 0 50px;
    padding-top: 20px;
  }

  @media (max-width: 1150px) {
    padding: 0 30px;
    padding-top: 20px;
  }

  @include less(tabletL) {
    padding: 0;
  }

  @import "header-mobile";
}

.header-bottom-decor {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 43px;
  width: 100%;
  clip-path: ellipse(62% 87% at 50% 100%);
  background-color: #f2fbdd;
}

.header-blocks {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

header {
  position: relative;
  align-items: flex-end;
  padding-bottom: 26px;

  .logo {
    max-width: 272px;
    width: 100%;
  }
}

.header {
  & .container {
    &:first-child {
      @include r($r-md) {
        padding: 0;
      }
    }
  }

  &__login {
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    padding: 11px 16px 10px 52px;
    position: relative;
    min-width: 130px;
    font-size: 14px;
    @include mon-semi;
    color: $header-title;

    @include r($r-md) {
      margin-left: auto;
      // margin-right: auto;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.3);
    }

    &:active {
      background-color: rgba(255, 255, 255, 0.7);
    }

    &::before {
      content: "";
      position: absolute;
      left: 28px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      mask-image: url("../img/header/login-icon.svg");
      background-color: $header-title;
      mask-repeat: no-repeat;
      mask-size: cover;

      @include r($r-md) {
        background-color: $login;
      }
    }

    @include r($r-md) {
      background: linear-gradient(
        135deg,
        rgba(160, 202, 63, 0.1) 0%,
        rgba(123, 156, 48, 0.1) 100%
      );
      color: $login;
      margin-bottom: 48px;
      margin-top: 24px;
    }
  }

  &__nav {
    position: relative;
    z-index: 10;
  }
}

.nav {
  width: 100%;

  @include rmin(769) {
    display: flex;
    align-items: flex-end;
    margin-bottom: 32px;
    justify-content: space-between;
  }

  @include r($r-md) {
    position: relative;
    width: 100vw;
  }

  &__mobile {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include r($r-md) {
      max-width: 1170px;
      padding: 0 15px;
      margin: 0 auto;
    }
  }

  &__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 50px;
    width: 50px;
    cursor: pointer;
    margin-right: -10px;
    align-self: flex-end;

    @include rmin($r-md) {
      padding: 0;
      display: none;
      margin-right: 0;
    }
  }

  &__line {
    position: relative;
    display: block;
    width: 30px;
    height: 4px;
    background-color: $t-main;
    transition: transform 0.3s ease-in-out;

    @include rmin(769) {
      display: none;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: $t-main;
    }

    &::before {
      top: 8px;
    }

    &::after {
      bottom: 8px;
    }

    &--active {
      background-color: transparent;

      &::before {
        transform: rotate(45deg);
        top: 50%;
      }

      &::after {
        transform: rotate(-45deg);
        top: 50%;
      }
    }
  }

  &__login {
    display: inline-block;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 4px;
    padding: 11px 16px 10px 52px;
    position: relative;
    min-width: 130px;
    font-size: 14px;
    font-family: Montserrat-Semibold, sans-serif;
    font-weight: 600;
    line-height: 2;
    color: $header-title;

    @include r($r-lg) {
      padding: 11px 10px 10px 22px;
      min-width: 80px;
    }

    @include r($r-md) {
      margin-left: auto;
      // margin-right: auto;
      margin-right: 16px;
      padding: 11px 16px 10px 52px;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.3);
    }

    &:active {
      background-color: rgba(255, 255, 255, 0.7);
    }

    &::before {
      content: "";
      position: absolute;
      left: 28px;
      top: 50%;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      mask-image: url("../img/icons/user.svg");
      background-color: $header-title;
      mask-repeat: no-repeat;
      mask-size: cover;

      @include r($r-lg) {
        left: 2px;
      }

      @include r($r-md) {
        background-color: $login;
        left: 28px;
      }
    }

    @include r($r-md) {
      background: linear-gradient(
        135deg,
        rgba(160, 202, 63, 0.1) 0%,
        rgba(123, 156, 48, 0.1) 100%
      );
      color: $login;
      margin-bottom: 48px;
      margin-top: 24px;
    }
  }

  &__links {
    display: flex;
    align-items: flex-end;
    margin-top: 3px;
    max-height: 0px;
    transition: max-height 0.3s ease-in-out;

    @include r($r-md) {
      background-color: $bg-main;
      flex-direction: column;
      overflow: hidden;
      position: absolute;
      left: 0px;
      width: 100%;
      top: 100%;
      z-index: 1;
      padding: 0 15px;
    }

    &--active {
      max-height: 750px;
    }
  }

  &__list {
    margin-right: 10px;

    @include r($r-md) {
      width: 100%;
      margin-top: 10px;
    }

    @include rmin(769) {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 34px;
    }

    @include rmin(1050) {
      margin-right: 98px;
    }
  }

  &__item {
    @include r($r-md) {
      text-align: right;
      width: 100%;

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }

    &:not(:last-child) {
      margin-right: 20px;

      @include r($r-lg) {
        margin-right: 5px;
      }

      @include r($r-md) {
        margin-right: 0px;
      }
    }

    &:not(:first-child) {
      margin-left: 20px;

      @include r($r-lg) {
        margin-right: 5px;
      }

      @include r($r-md) {
        margin-left: 0px;
      }
    }

    &--solution {
      position: relative;
      cursor: pointer;

      @include r($r-md) {
        padding-right: 32px;
      }

      &::after {
        transition: transform 0.3s ease-in-out;

        content: "";
        position: absolute;
        width: 8px;
        height: 5px;
        background-image: url("../img/icons/toggle.svg");
        background-repeat: no-repeat;
        background-size: contain;
        cursor: pointer;
        transform: rotate(0deg);
        top: 8px;

        @include r($r-md) {
          right: 10px;
        }

        @include rmin($r-md) {
          right: 12px;
        }
      }
    }

    &--resources {
      &::after {
        display: none;
      }
    }

    &--active {
      &::after {
        transform: rotate(180deg);
      }
    }
  }

  &__link {
    transition: background-color 0.3s ease-in-out;
    padding: 4px 8px;
    border-radius: 4px;
    position: relative;
    font-size: 17px;

    &:hover {
      background-color: rgba(#ffffff, 0.15);
    }

    &:active {
      background-color: rgba(#ffffff, 0.05);
    }

    &--toggle {
      padding-right: 28px;

      @include r($r-md) {
        padding-right: 1px;
      }
    }
  }

  &__droplist {
    width: 526px;
    flex-wrap: wrap;
    display: none;
    position: absolute;
    background-color: $header-title;
    box-shadow: 0px 9px 31px rgba(0, 0, 0, 0.07),
      0px 1.12694px 3.88168px rgba(0, 0, 0, 0.035);
    border-radius: 4px;
    top: 100%;
    left: 0;
    z-index: 1;

    @include r($r-md) {
      width: 100%;
      background-color: transparent;
      box-shadow: none;
      position: relative;
      right: -32px;
      left: auto;
      margin-left: auto;
    }

    &--resources {
      li:first-child {
        display: flex;
        justify-content: space-between;

        @include r($r-md) {
          flex-direction: column;
          width: 100%;
        }

        li {
          padding: 8px 16px;
        }
      }

      & .nav {
        &__dropcol {
          &:first-child {
            border-right: 2px solid #f0f0f0;

            @include r($r-md) {
              border-right: none;
            }

            @include rmin($r-md) {
              max-width: 300px;
              width: 100%;
            }
          }
        }

        &__dropitem {
          width: 100%;
          min-height: 124px;

          &:nth-child(3) {
            border-right: none;
          }

          @include r($r-md) {
            min-height: 20px;
          }

          &--top {
            padding: 8px 16px;
            min-height: 0px;
            border: none;
            color: $btn-bg;
            font-size: 12px;
            @include mon-bold;
            line-height: 1.7;

            &:hover {
              transition: none !important;
              background-color: white !important;
              cursor: auto !important;
            }
            &:nth-child(1) {
              border-right: none;
            }
          }
        }

        &__dropheading {
          padding-left: 0;
          @include mon-medium;
          line-height: 1.5;
          display: block;
        }
      }
    }

    &.active {
      display: flex;
    }
  }

  &__dropitem {
    width: calc(100% / 2);
    padding: 12px;
    transition: background-color 0.3s ease-in-out;

    @include r($r-md) {
      width: 100%;
      padding: 8px;
    }

    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4) {
      // border-bottom: 1px solid #f0f0f0;
      @include r($r-md) {
        border-bottom: none;
      }
    }

    &:nth-child(1),
    &:nth-child(3),
    &:nth-child(5) {
      border-right: 1px solid #f0f0f0;

      @include r($r-md) {
        border-right: none;
      }
    }

    &:nth-child(5),
    &:nth-child(6) {
      padding-bottom: 16px;

      @include r($r-md) {
        padding-bottom: 8px;
      }
    }

    &:hover {
      @include rmin(769) {
        background-color: $drophover;
      }
    }

    &--top {
      width: 100%;
      padding: 0;

      ul {
        display: flex;
        justify-content: space-between;
        list-style: none;
        color: $btn-bg;
        width: 50%;
      }

      // li{
      //   width: 50%;
      //   padding: 8px 16px;
      //   &:not(:last-child){
      //     border-right: 1px solid #f0f0f0;
      //   }
      // }
    }
  }

  &__dropheading {
    position: relative;
    font-size: 16px;
    line-height: 2;
    @include roboto-regular;
    color: $title;
    padding-left: 26px;
    margin-bottom: 4px;

    @include r($r-md) {
      @include mon-regular;
    }

    &::before {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      left: 0;
      top: 2px;
      background-repeat: no-repeat;
      background-size: contain;

      @include r($r-md) {
        display: none;
      }
    }

    &--star {
      &::before {
        background-image: url("../img/icons/solutions/star.svg");
      }
    }

    &--location {
      &::before {
        background-image: url("../img/icons/solutions/location.svg");
      }
    }

    &--mail {
      &::before {
        background-image: url("../img/icons/solutions/mail.svg");
      }
    }

    &--box {
      &::before {
        background-image: url("../img/icons/solutions/box.svg");
      }
    }

    &--testing {
      &::before {
        background-image: url("../img/icons/solutions/ab.svg");
      }
    }

    &--dashboard {
      &::before {
        background-image: url("../img/icons/solutions/dashboard.svg");
      }
    }
  }

  &__droptext {
    font-size: 12px;
    line-height: 1.3;
    color: $droptext;
    @include roboto-regular;

    @include r($r-md) {
      display: none;
    }
  }
}

.logo {
  max-width: 272px;
  height: 77px;

  @include r($r-md) {
    max-width: 172px;
  }

  &__img {
    max-width: 100%;
    min-width: 170px;
  }
}

.header-bottom {
  align-items: flex-start;

  @include less(tabletL) {
    padding: 0 16px;
  }

  .btns {
    a {
      .btn {
        color: white;
        background-color: $secondColor;
        transition: background-color $animTime ease-in-out,
          color $animTime ease-in-out;

        &:hover {
          background-color: #691341;
        }
      }
    }
  }
}

.header-bottom-left {
  max-width: 483px;

  h1 {
    padding-top: 30px;
    text-align: start;
    color: white;
    @include adap-font(36, 32);

    @include less(mobileL) {
      line-height: 35px;
      padding-top: 24px;
    }

    p {
      width: 100%;
      color: #b7dc62;
      font-size: 28px;
      text-align: end;
      padding-top: 10px;

      @include less(mobileL) {
        padding-right: 6px;
      }
    }
  }

  p {
    padding-top: 50px;
    font-family: $fontText;
    @include adap-font(18, 16);
    color: $primaryColor;
    line-height: 35px;

    @media (max-width: 1350px) {
      padding-top: 20px;
    }

    @include less(tabletL) {
      padding-top: 22px;
    }

    @include less(mobileL) {
      line-height: 30px;
    }

    span {
      font-weight: 600;
    }
  }

  .text {
    padding-top: 10px;
  }

  .header-btns-msg {
    display: flex;
    align-items: center;
    width: max-content;
    margin-top: 29px;

    .header-bottom-left-msg {
      padding-top: 0;
      font-size: 14px;
      line-height: 136%;
    }
  }
}

.btns-mobile {
  display: none;
}

.header-btns:first-child {
  color: white;
  background-color: $secondColor;
}

.header-btns:last-child {
  color: $secondColor;
  background: #ffffff33;
}

.header-bottom-right {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 500px;
  width: 49%;

  @include less(tabletS) {
    height: 400px;
  }

  @include less(tabletL) {
    display: none;
  }

  &:hover {
    .top {
      @include pref(transform, rotate(4deg));
    }

    .bottom {
      @include pref(transform, rotate(-10deg));
    }
  }

  .header-right-img {
    position: absolute;
    transition: $animTime transform ease-in-out;
  }

  .top {
    top: 10%;
    z-index: 3;

    @include less(laptopS) {
      top: 19%;
      max-width: 600px;
    }

    @media (max-width: 1170px) {
      top: 22%;
      max-width: 500px;
    }
  }

  .bottom {
    @include less(laptopS) {
      max-width: 550px;
    }

    @media (max-width: 1170px) {
      max-width: 450px;
    }
  }
}

.header-bottom-right-mob {
  display: none;

  @include less(tabletL) {
    display: block;

    img {
      margin-bottom: 25px;

      @include less(tablet) {
        max-width: 430px;
      }

      @media (max-width: 410px) {
        max-width: 380px;
      }

      @media (max-width: 370px) {
        max-width: 330px;
      }
    }
  }
}
@media screen and (min-width: 1440px) {
  header .container {
    max-width: 1140px;
    width: 100%;
  }
}
