.faq {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;
  padding-top: 64px;
  padding-bottom: 58px;
  background: $bgColor;
  box-shadow: 0px 62px 80px rgba(0, 0, 0, 0.07);
}

.faq-content {
  position: relative;
  display: flex;
  justify-content: center;
  padding-top: 40px;
  color: $primaryColor;
  font-family: $fontText;
  @include less(tabletL) {
    display: none;
  }

  .left {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 26%;
    margin-right: 32px;
    z-index: 2;

    @include less(laptopS) {
      width: 40%;
    }
    @include less(tabletL) {
      width: 80%;
    }

    p {
      margin-top: 24px;
      cursor: pointer;
      &:first-child {
        margin-top: 0;
      }
    }

    .active {
      font-weight: 700;
    }
  }

  .right {
    position: relative;
    height: 377px;
    max-width: 825px;
    padding: 24px;
    background: #a8c170;
    border-radius: 8px;
    box-shadow: 0px 8px 20px 0px #00000038;
    z-index: 2;
    overflow: auto;
    @include less(laptopS) {
      width: 60%;
    }
    @include less(tabletL) {
      width: 88%;
      margin-top: 20px;
    }

    p {
      display: none;
    }

    .active {
      display: block;
    }
  }
  &__indention {
    display: block;
    margin-top: 24px;
    margin-bottom: 24px;
  }
  img {
    position: absolute;
    top: 50%;
    left: 35%;
    transform: translate(-50%, -50%);
    @include less(tabletL) {
      left: 50%;
    }
  }
}

.faq-content-mob {
  display: none;
  margin-top: 20px;
  color: $primaryColor;
  font-family: $fontText;

  @include less(tabletL) {
    display: flex;
    justify-content: center;

    .box {
      width: 80%;
      p {
        margin-top: 24px;
        cursor: pointer;
        &:first-child {
          margin-top: 0;
        }
      }

      p.active {
        font-weight: 700;
      }

      .answ {
        height: 0;
        max-width: 825px;
        padding: 0 24px;
        margin-top: 20px;
        background: #a8c170;
        border-radius: 8px;
        box-shadow: 0px 8px 20px 0px #00000038;
        z-index: 2;
        overflow: auto;
        opacity: 0;
        visibility: hidden;
        transition: $animTime height ease-in-out, $animTime opacity ease-in-out,
          $animTime visibility ease-in-out, $animTime padding ease-in-out;
      }

      .answ.active {
        height: 377px;
        padding: 24px;
        margin-top: 20px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
