.marketplace {
  padding: 64px 20px 88px;
  background: #f2fbdd;
  h1 {
    max-width: 1100px;
    margin: 0 auto;
  }
  div {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 40px;
    svg {
      height: 32px;
      width: 55px;
      margin-right: 16px;
      margin-bottom: 10px;
      &:last-child {
        margin-right: 0;
      }
    }
  }
}
