.customer {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 64px;
  padding-bottom: 120px;
  background: $bgColor;
  overflow: hidden;

  h1 {
    padding: 0 10px;
    @include adap-font(38, 24);
    color: white;
    z-index: 6;
  }
}

.customer-container {
  position: relative;
  display: flex;
  align-items: center;
  margin-top: 20px;

  .tabs {
    position: relative;
    min-width: 720px;
    z-index: 2;

    @media (max-width: 830px) {
      min-width: 0;
      width: 600px;
    }

    @media (max-width: 670px) {
      width: 500px;
    }
    @media (max-width: 570px) {
      width: 400px;
    }
    @media (max-width: 500px) {
      width: 330px;
    }
    @media (max-width: 390px) {
      width: 280px;
    }

    .tabs-header {
      display: flex;
      align-items: center;
      margin-bottom: -1px;
      width: 100%;
      .tab {
        position: relative;
        display: flex;
        align-items: center;
        height: 50px;
        width: 33.33%;
        font-family: $fontText;
        padding: 13px 16px;
        border-top-left-radius: 8px;
        border-top-right-radius: 8px;
        background-color: #cae97b;
        cursor: pointer;

        @include less(tabletL) {
          width: auto;
          padding: 16px;
        }

        &:nth-child(2).active:before,
        &:last-child.active:before {
          content: "";
          position: absolute;
          height: 15px;
          width: 30px;
          bottom: -1px;
          left: -30px;
          border-radius: 0 0 42px 0;
          box-shadow: 19px 0 0 0 #fff;
          z-index: 3;
        }

        &:first-child:not(.active):after {
          z-index: 3;
          box-shadow: -10px 0 0 0 #cae97b;
        }

        &:nth-child(2):not(.active):after {
          z-index: 1;
          box-shadow: -10px 0 0 0 #e3f2bc;
        }

        &:last-child:after {
          content: none;
        }

        &:after {
          content: "";
          position: absolute;

          height: 10px;
          width: 20px;

          bottom: 0;
          right: -20px;

          border-radius: 0 0 0 10px;
          box-shadow: -10px 0 0 0 #fff;
          z-index: 2;
        }
        &:nth-child(2):not(.active) {
          background: #e3f2bc;
        }

        &.active:after,
        &.active:before {
          z-index: 5;
        }

        svg {
          width: 24px;
          height: 24px;
          padding-right: 8px;
          @include less(tabletL) {
            padding: 0;
          }
        }

        p {
          @include adap-font(14, 10);
          @include less(tabletL) {
            display: none;
          }
        }
      }

      span {
        font-weight: 600;
      }

      .active {
        background-color: white;
        @include less(tabletL) {
          width: 40%;
          svg {
            margin-right: 8px;
          }
        }
        img {
          padding-right: 8px;
        }
        p {
          @include less(tabletL) {
            display: block;
          }
        }
      }
    }

    .tabs-body {
      width: 100%;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px;
      overflow: hidden;
      .content {
        display: none;
      }
      .active {
        display: block;
        background-color: white;

        .slider {
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          height: 450px;
          max-width: 720px;
          padding: 30px 32px;
          padding-bottom: 26px;
          @include less(tablet) {
            height: 340px;
            padding: 15px 18px;
          }
          @media (max-width: 670px) {
            height: 330px;
            padding: 10px;
          }
          @media (max-width: 600px) {
            height: 310px;
            padding: 10px;
          }
          @media (max-width: 570px) {
            height: 280px;
          }

          .slider-body {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height: 100%;
            @include less(tablet) {
              height: 222px;
            }
            @media (max-width: 579px) {
              height: 200px;
            }

            .slid {
              display: none;
              flex-direction: column;
              justify-content: center;
              align-content: center;
              height: 301px;
              max-width: 620px;
              width: 100%;
            }

            .trust {
              font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
                Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
                sans-serif;

              @include less(tablet) {
                margin-top: 60px;
              }
              @media (max-width: 579px) {
                margin-top: 30px;
              }

              hr {
                margin: 14px 0;
                @media (max-width: 579px) {
                  margin: 8px 0;
                }
              }

              .header {
                display: flex;
                .ava-img {
                  height: 40px;
                  width: 40px;
                  max-width: 40px;
                  margin-right: 7px;
                }

                .ava {
                  display: flex;
                  justify-content: center;
                  align-items: center;
                  height: 40px;
                  width: 40px;
                  margin-right: 7px;
                  border-radius: 50%;
                  background: #e3effb;
                }

                p {
                  @include adap-font(12, 8);
                  color: #83838e;

                  &:last-child {
                    display: flex;
                    align-items: center;
                  }
                }
                .name {
                  margin-bottom: 7px;
                  color: #454554;
                }

                svg {
                  max-width: 14px;
                  margin-right: 5px;
                }
              }

              .body {
                padding-right: 5px;

                div {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;
                  .stars {
                    margin-right: 16px;
                  }

                  p {
                    color: #9696a1;
                    margin-top: 0;
                    img {
                      margin-right: 4px;
                    }
                  }
                }

                p {
                  margin-top: 19px;
                  @include adap-font(13, 8);
                  @include less(tablet) {
                    margin-top: 12px;
                  }
                  @media (max-width: 579px) {
                    margin-top: 6px;
                  }
                }

                .mes {
                  max-height: 83px;
                  padding-right: 10px;
                  @include adap-font(14, 8);
                  line-height: 27px;
                  overflow: auto;
                  @include less(tablet) {
                    line-height: 10px;
                  }
                }
              }

              .footer {
                display: flex;
                flex-direction: column;
                .top {
                  display: flex;
                  justify-content: space-between;
                  padding-right: 15px;

                  div {
                    display: flex;
                  }
                  p {
                    display: flex;
                    align-items: center;
                    @include adap-font(13, 8);
                    color: #9696a1;
                    img {
                      margin-right: 4px;
                    }
                    &:first-child {
                      margin-right: 20px;
                    }
                  }
                }
                .bottom {
                  margin-top: 10px;
                  padding: 16px;
                  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
                  color: #73738f;
                  border-left: 3px solid #1a66ff;
                  background-color: #f2f2f5;

                  @include less(tabletL) {
                    padding: 8px;
                  }
                  p {
                    @include adap-font(14, 8);
                    &:last-child {
                      margin-left: 20px;
                    }
                  }
                  div {
                    display: flex;
                    justify-content: space-between;
                    white-space: nowrap;
                    padding-bottom: 6px;

                    @include less(tabletL) {
                      padding-bottom: 2px;
                    }
                    svg {
                      max-width: 12px;
                      width: 100%;
                      margin-right: 8px;
                    }

                    h4 {
                      color: #454554;
                      @include adap-font(16, 10);
                    }
                  }
                }
              }
            }

            .amazon {
              max-width: 100%;

              div {
                font-family: -apple-system, BlinkMacSystemFont, "Segoe UI",
                  Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans",
                  "Helvetica Neue", sans-serif;

                p {
                  margin-top: 12px;
                  @include less(tablet) {
                    margin-top: 7px;
                  }
                }

                .name {
                  @include adap-font(14, 8);
                }

                .review {
                  display: flex;
                  align-items: center;
                  img {
                    margin-top: 12px;
                    margin-right: 13px;
                    @include less(tablet) {
                      margin-top: 7px;
                    }
                  }
                  p {
                    font-weight: 700;
                    @include adap-font(12, 8);
                  }
                }

                .date {
                  @include adap-font(14, 8);
                }

                .status {
                  @include adap-font(13, 9);
                  color: #fe6138;
                }
                .mes {
                  line-height: 24px;
                  @include adap-font(14, 8);

                  @include less(tablet) {
                    line-height: 14px;
                  }
                }
              }
            }

            .feedback {
              height: 335px;
              margin: 0 auto;
              padding: 20px 25px;
              padding-right: 5px;
              box-shadow: 0px 7.32432px 25.2282px rgba(0, 0, 0, 0.07);
              @include less(tablet) {
                padding: 15px 10px;
                margin-top: 60px;
              }
              @media (max-width: 670px) {
                margin-top: 30px;
              }
              @media (max-width: 500px) {
                padding: 10px;
              }

              .ava {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 40px;
                width: 40px;
                margin-right: 10px;
                border-radius: 50%;
                color: white;
                @media (max-width: 670px) {
                  height: 25px;
                  width: 25px;
                }
              }

              &:first-child {
                .ava {
                  color: black;
                  background-color: #f9dab9;
                }
              }
              &:nth-child(2) {
                .ava {
                  background-color: #513782;
                }
              }
              &:nth-child(3) {
                .ava {
                  color: silver;
                  background-color: #7ff9fb;
                }
              }
              &:nth-child(4) {
                .ava {
                  background-color: #ed8b34;
                }
              }
              &:nth-child(5) {
                .ava {
                  background-color: #6dcdaa;
                }
              }
              &:nth-child(6) {
                .ava {
                  background-color: #ed8b34;
                }
              }
              &:nth-child(7) {
                .ava {
                  background-color: #87ceeb;
                }
              }
              &:nth-child(8) {
                .ava {
                  background-color: #e55290;
                }
              }
              &:nth-child(9) {
                .ava {
                  background-color: #488109;
                }
              }
              &:nth-child(10) {
                .ava {
                  background-color: #63b472;
                }
              }
              &:last-child {
                .ava {
                  color: black;
                  background-color: #87e910;
                }
              }
              .mes {
                display: flex;
                font-family: $fontText;
                @include adap-font(14, 8);
                margin-top: 26px;
                @include less(mobileL) {
                  margin-top: 16px;
                }
                @include less(tablet) {
                  margin-top: 10px;
                }

                &:last-child {
                  p {
                    &:last-child {
                      margin-top: 10px;
                      padding: 0;
                      padding-left: 6px;
                      border-left: 4px solid #81a332;
                    }
                  }
                }

                img {
                  height: 40px;
                  width: 40px;
                  margin-right: 13px;
                  border-radius: 50%;
                  object-fit: cover;

                  @media (max-width: 670px) {
                    height: 25px;
                    width: 25px;
                  }
                }

                .text {
                  display: flex;
                  flex-direction: column;
                  max-width: 80%;
                  p {
                    padding-top: 6px;
                    @include adap-font(14, 8);
                    @include less(tablet) {
                      padding-top: 3px;
                    }
                    &:first-child {
                      padding-top: 0;
                    }
                  }
                  .role {
                    @include adap-font(12, 8);

                    color: #81a332;
                  }
                }

                p:first-child {
                  display: flex;
                  color: #79808d;
                  span {
                    padding-right: 13px;
                    font-weight: 600;
                    color: black;
                  }
                }
              }
            }

            .active {
              display: flex;
            }
          }

          .slider-panel {
            display: flex;
            justify-content: flex-end;
            padding-top: 22px;

            .left,
            .right {
              padding: 7px 12px;
              background: #81a332;
              border-radius: 8px;
              cursor: pointer;
              user-select: none;
              @media (max-width: 500px) {
                padding: 3px 8px;
              }
            }

            .right {
              margin-left: 13px;
              transform: rotate(180deg);
            }

            .count {
              display: flex;
              align-items: flex-end;
              justify-content: center;
              padding-bottom: 5px;
              margin-left: 13px;
              div {
                height: 6px;
                width: 6px;
                margin-right: 12px;
                background: #c4c4c4;
                border-radius: 50%;
                cursor: pointer;
                &:last-child {
                  margin-right: 0;
                }
              }
              .active {
                background: #81a332;
              }
            }
          }
        }
      }
    }
  }

  .bg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 885px;

    @media (max-width: 920px) {
      max-width: 800px;
    }
    @media (max-width: 870px) {
      max-width: 750px;
    }
    @media (max-width: 780px) {
      max-width: 700px;
    }
    @media (max-width: 730px) {
      max-width: 620px;
    }
    @media (max-width: 670px) {
      top: 55%;
      width: 530px;
    }
  }
}
