footer {
  display: flex;
  justify-content: center;
  padding-top: 33px;
  padding-bottom: 98px;
  font-family: $fontText;
  font-size: 14px;
  color: white;
  background: #191d20;
  overflow: hidden;
  @media (max-width: 1550px) {
    padding: 100px;
    padding-top: 33px;
    padding-bottom: 98px;
  }
  @include less(tabletL) {
    flex-direction: column;
    align-items: center;
  }
  @include less(mobileL) {
    padding: 10px 20px;
  }

  .container-small {
    display: flex;
    flex-direction: row;
    justify-content: center;
    .left {
      margin-right: 385px;
      @media (max-width: 1150px) {
        margin-right: 185px;
      }
      @include less(tabletL) {
        margin-right: 0;
      }

      .footer-right.mobile {
        display: none;
        @include less(tabletL) {
          display: flex;
        }
      }
      .footer-text {
        display: flex;
        flex-direction: column;
        max-width: 400px;
        padding-top: 64px;
        line-height: 25px;
        letter-spacing: 0.5px;
        p {
          &:last-child {
            margin-top: 32px;
            white-space: nowrap;
            @include less(mobileL) {
              white-space: normal;
            }
          }
        }
        .links {
          margin-top: 32px;
          color: #afdd44;
          a {
            margin: 0 7px;
            color: #afdd44;

            &:first-child {
              margin-left: 0;
            }
            &:last-child {
              margin-right: 0;
            }
          }
        }
      }
    }
    .footer-right {
      display: flex;
      flex-direction: column;
      padding-top: 65px;
      @include less(tabletL) {
        display: none;
      }
      p {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 34px;

        img {
          display: flex;
          justify-content: center;
          height: 23px;
          width: 32px;
          margin-right: 10px;
        }

        a {
          width: 200px;
          color: white;
          font-weight: 400;
          line-height: 24px;
        }
      }
      .social {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 67px;
        a {
          margin-right: 40px;
          img {
            max-width: 40px;
            width: 100%;
          }
          &:last-child {
            margin-right: 0;
          }
        }
      }
    }
  }
}
