.solution {
  padding: 0 20px;
  padding-top: 64px;
  padding-bottom: 96px;
  background: $bgColor;
  overflow: hidden;
  @include less(tabletL) {
    padding-bottom: 48px;
  }
}

.solution h1 {
  color: white;
}

.solution-content {
  position: relative;
  display: grid;
  grid-template-columns: 433px 433px;
  gap: 112px;
  row-gap: 72px;
  align-items: center;
  justify-items: center;
  justify-content: center;
  padding-top: 72px;

  @media (max-width: 1150px) {
    gap: 50px;
    row-gap: 42px;
  }
  @include less(tabletL) {
    grid-template-columns: 360px;
    padding-top: 24px;
    gap: 16px;
  }
  @media (max-width: 380px) {
    grid-template-columns: 290px;
  }

  .items {
    display: flex;
    align-items: center;
    height: 186px;
    width: 100%;
    padding: 42px 20px;
    border-radius: 8px;
    background: #f2fbdd;
    z-index: 4;

    &:nth-child(2) {
      img {
        @include less(tabletL) {
          max-width: 122px;
        }
      }
    }

    @media (max-width: 1150px) {
      height: 180px;
      padding: 32px 20px;
    }
    img {
      max-width: 157px;
      width: 100%;
      padding-right: 48px;
      @media (max-width: 1150px) {
        padding-right: 28px;
      }
      @include less(tablet) {
        max-width: 140px;
      }
      @media (max-width: 380px) {
        max-width: 110px;
      }
    }
  }
  .bg {
    position: absolute;
    top: 59%;
    left: 50%;
    max-width: 848px;
    transform: translate(-50%, -50%);

    @media (max-width: 865px) {
      max-width: 700px;
    }
    @media (max-width: 730px) {
      max-width: 500px;
    }
  }
}

.solution-text {
  font-family: $fontText;
  @include adap-font(24, 20);
  color: $primaryColor;
  line-height: 40px;
  @media (max-width: 1150px) {
    line-height: 35px;
  }
  @include less(tabletL) {
    line-height: 25px;
  }
}

.solution-text span {
  font-weight: bold;
}
