.automate {
  padding: 64px 192px;
  background: #f2fbdd;
  overflow: hidden;
  @include less(laptopM) {
    padding: 0 50px;
    padding-top: 64px;
    padding-bottom: 88px;
  }
  @include less(tabletS) {
    padding: 0 20px;
    padding-top: 44px;
    padding-bottom: 48px;
  }

  .title {
    @include adap-font(38, 24);
    padding-bottom: 0;
  }

  .automate-content {
    display: flex;
    justify-content: center;
    max-width: 1216px;
    width: 100%;
    margin: 0 auto;
    padding-top: 40px;
    border-radius: 8px;
    box-shadow: 0px 67px 55px -22px rgba(0, 0, 0, 0.07);
    @include less(tabletL) {
      flex-direction: column;
      align-items: center;
      box-shadow: none;
      padding-top: 24px;
    }
    .automate-left {
      padding: 24px 16px;
      background-color: white;
      @include less(tabletL) {
        width: 60%;
        padding-top: 30px;
      }
      @include less(tablet) {
        width: 80%;
      }
      @include less(tabletS) {
        width: 100%;
      }
      @include less(mobileL) {
        padding: 30px 18px;
        padding-top: 0;
      }
      .automate-title {
        padding-left: 30px;
        font-family: $fontTitle;
        @include adap-font(32, 20);
        font-weight: 700;
        color: $secondColor;
        @include less(tabletL) {
          display: none;
        }
      }
      .automate-content-left {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 65px;
        @include less(tabletL) {
          flex-direction: column;
          align-items: center;
        }
        img {
          max-width: 414px;
          width: 100%;
          @media (max-width: 1110px) {
            max-width: 400px;
          }
          @include less(mobileL) {
            max-width: 270px;
          }
        }
        h2 {
          display: none;
          padding-left: 30px;
          font-family: $fontTitle;
          @include adap-font(32, 20);
          font-weight: 700;
          color: $secondColor;
          @include less(tabletL) {
            display: block;
          }
          @include less(mobileL) {
            padding-left: 0;
          }
        }

        div {
          width: 379px;
          padding: 16px;
          @include less(mobileL) {
            width: auto;
          }
          p {
            @include adap-font(18, 18);
            color: $primaryColor;
            font-family: $fontText;
            line-height: 29px;
            span {
              display: block;
              margin-top: 24px;
            }
          }
        }
      }
    }
    .automate-content-right {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      width: 33%;
      padding: 24px;
      background: $secondColor;
      @include less(tabletL) {
        width: 60%;
      }
      @include less(tablet) {
        width: 80%;
      }
      @include less(tabletS) {
        width: 100%;
      }
      @include less(mobileL) {
        padding: 30px 18px;
        padding-top: 20px;
      }
      h3 {
        font-family: $fontText;
        @include adap-font(18, 18);
        color: white;
      }
      div {
        display: flex;
        padding-top: 32px;
        font-family: $fontText;
        font-weight: 400;
        color: white;
        img {
          padding-right: 16px;
        }
        p {
          @include adap-font(16, 16);
        }
      }

      a {
        .btn {
          padding: 14px 24px;
          font-weight: 600;
          @media (max-width: 380px) {
            padding: 12px 14px;
          }
        }

        &:first-child {
          .btn {
            background-color: white;
            color: $secondColor;
            transition: color $animTime ease-in-out,
              background-color $animTime ease-in-out;
            &:hover {
              color: white;
              background-color: #691341;
            }
          }
        }
        &:last-child {
          .btn {
            color: white;
            background-color: transparent;
            border: 1px solid white;
            transition: color $animTime ease-in-out,
              background-color $animTime ease-in-out,
              border-color $animTime ease-in-out;
            &:hover {
              border-color: #691341;
              background-color: #691341;
            }
          }
        }
      }
    }
  }
  .automate-msg {
    padding-top: 24px;
    font-family: $fontText;
    @include adap-font(16, 12);
    color: $secondColor;
  }
}
