.plan {
  padding: 64px 20px 88px;
  overflow: hidden;

  @include less(tabletL) {
    padding-bottom: 40px;
  }

  .subTitle {
    font-weight: normal;
  }

  .plan-content {
    position: relative;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 60px;
    width: 100%;
    @include less(tabletL) {
      gap: 0;
      grid-template-rows: 1fr 400px;
      justify-content: center;
      justify-items: center;
      padding-top: 60px;
    }

    .plan-top {
      position: relative;
      height: 145px;
      margin-top: 64px;
      user-select: none;
      grid-column: span 2;

      @include less(tabletL) {
        grid-column: unset;
        margin-top: 0;
      }

      .background {
        display: flex;
        .bg {
          height: 145px;
          padding: 10px 5px;
          margin-right: 5px;
          font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
            Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
          @include adap-font(24, 16);
          font-weight: 500;
          color: #79808d;
          &:last-child {
            margin-right: 0;
          }
        }
        .free {
          width: 12%;
          background: #afdd441f;
        }

        .basic {
          width: 30%;
          background: linear-gradient(
            270deg,
            rgba(129, 163, 50, 0.12) 0%,
            rgba(174, 220, 68, 0.12) 100%
          );
        }

        .premium {
          width: 65%;
          background: linear-gradient(
            90deg,
            rgba(119, 145, 48, 0.12) 0%,
            rgba(211, 85, 152, 0.12) 34.9%,
            rgba(149, 24, 89, 0.12) 70.31%,
            rgba(230, 75, 60, 0.12) 100%
          );
        }
      }

      .range-content {
        position: absolute;
        height: 320px;
        width: 100%;
        bottom: -120%;
        .range {
          position: absolute;
          top: 39%;
          left: 0;
          display: flex;
          justify-content: space-between;
          align-items: center;
          height: 20px;
          width: 100%;
          padding: 7px 5px;
          background: #e6f1cc;
          border-radius: 12px;
          cursor: pointer;

          .range-decor {
            height: 10px;
            width: 15.8333333333%;
            border-radius: 8px;
            background: #afdd44;

            &:first-child {
              width: 4.2%;
            }

            &:nth-child(2) {
              width: 14.7%;
              background: linear-gradient(90deg, #afdd44 0%, #81a332 100%);
            }
            &:nth-child(3) {
              width: 14.7%;
              background: #81a332;
            }
            &:nth-child(4) {
              background: linear-gradient(90deg, #81a332 0%, #d35598 100%);
            }
            &:nth-child(5) {
              background: #d35598;
            }
            &:nth-child(6) {
              background: linear-gradient(90deg, #d35598 0%, #941759 100%);
            }
            &:nth-child(7) {
              background: linear-gradient(270deg, #e74c3c 0%, #94185a 100%);
            }
          }

          .range-text {
            position: absolute;
            left: 0;
            bottom: -26px;
            display: flex;
            justify-content: space-between;
            width: 100%;
            padding-right: 5px;
            font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
              Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
              sans-serif;
            color: #555a64;
            div {
              display: flex;
              p {
                &:first-child {
                  margin-right: 20px;
                }
              }
            }
          }

          .range-btn {
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            height: 32px;
            width: 32px;
            border: 4px solid #953459;
            border-radius: 50%;
            background: #f2f2f2;

            div {
              display: flex;
              align-items: center;
              justify-content: center;
              position: absolute;
              top: -35px;
              padding: 3px 9px;
              font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
                Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
                sans-serif;
              font-size: 12px;
              font-weight: 500;
              color: #333945;
              border: 4px solid #953459;
              border-radius: 8px;
              background: white;
            }
          }
        }
      }
    }

    .plan-btn {
      margin-top: 20px;
      padding: 9px 14px;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
        Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
      font-weight: 500;
      color: white;
      background: #b11c6b;
      border-radius: 2px;
      text-align: center;
      transition: background-color $animTime ease-in-out;
      &:hover {
        background: #861752;
      }
    }

    .body {
      display: flex;
      flex-direction: column;
      margin-top: 10px;

      div {
        display: flex;
        align-items: center;
        margin-top: 10px;
        &:first-child {
          margin-top: 0;
        }

        svg {
          width: 10px;
          height: 10px;
          margin-right: 14px;
        }
      }
    }

    .left {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 553px;
      margin-right: 55px;
      @include less(laptopS) {
        margin-right: 30px;
      }
      @include less(tabletL) {
        margin: 0;
        grid-column: span 2;
      }
      .content {
        position: inherit;
        width: 100%;
        max-width: 500px;
        padding: 20px;
        font-family: $fontText;
        background-color: white;
        border-radius: 11px;
        box-shadow: 0px 62px 80px 0px #00000012;
        z-index: 2;

        @include less(tabletL) {
          max-width: 300px;
        }

        .header {
          display: flex;
          justify-content: space-between;

          div {
            h3 {
              font-size: 24px;
              @include adap-font(24, 18);

              font-weight: normal;
            }

            .plan-subTitle {
              margin-top: 25px;
              font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
                Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
                sans-serif;
              @include adap-font(12, 10);
              font-weight: 600;
              color: $secondColor;
              white-space: nowrap;
              @include less(tabletL) {
                white-space: normal;
              }
              span {
                margin-right: 40px;
                font-weight: normal;
                @include less(tabletL) {
                  margin-right: 20px;
                }
              }
            }

            &:last-child {
              margin-left: 35px;
              white-space: nowrap;
              h3 {
                @include adap-font(28, 20);

                font-weight: 500;
                text-align: end;
                color: #81a332;
              }
              @include less(laptopS) {
                margin-left: 30px;
              }
            }
          }
        }

        .body {
          @include adap-font(16, 14);

          @include less(tabletL) {
            div {
              display: flex;
              margin-top: 7px;
              &:first-child {
                margin: 0;
              }
            }
          }
        }
      }

      .bg {
        position: absolute;
        max-width: 553px;
      }
    }

    .right {
      @include less(tabletL) {
        grid-row: 1;
        grid-column: 2;
        // padding-right: 60px;
      }

      .content {
        display: none;
        font-family: $fontText;
        padding: 20px;
        width: 400px;

        @include less(tabletL) {
          width: 167px;
        }

        .header {
          display: flex;
          justify-content: space-between;

          @include less(tabletL) {
            justify-content: center;
          }
          div {
            h3 {
              font-size: 24px;
              font-weight: normal;
            }

            &:first-child {
              p {
                margin-top: 10px;
                color: #81a332;
                font-weight: 600;
                white-space: nowrap;
              }
            }

            &:last-child {
              margin-left: 150px;
              white-space: nowrap;

              @media (max-width: 1365px) {
                margin-left: 0;
              }

              @include less(tabletL) {
                display: none;
              }
              h3 {
                margin-left: 150px;
                font-size: 28px;
                font-weight: 500;
                text-align: end;
                color: $primaryColor;
                @media (max-width: 1365px) {
                  margin-left: 0;
                }

                @include less(tabletL) {
                  margin-left: 50px;
                }
              }

              span {
                padding-right: 8px;
                color: #81a332;
              }
            }
          }
        }

        .body {
          div {
            @include less(tabletL) {
              margin-top: 20px;
            }

            p {
              @include adap-font(16, 12);
            }

            &:last-child {
              white-space: nowrap;
              display: none;
              @include less(tabletL) {
                display: flex;
                justify-content: center;
              }
              h3 {
                margin-left: 150px;
                @include adap-font(28, 20);

                font-weight: 500;
                text-align: end;
                color: $primaryColor;
                @include less(tabletL) {
                  margin-left: 0;
                }
              }

              span {
                padding-right: 8px;
                color: #81a332;
              }
            }
          }
        }
      }
      .free {
        display: block;
        .header {
          div {
            &:last-child {
              h3 {
                color: #81a332;
              }
            }
          }
        }

        @include less(tabletL) {
          .body {
            div {
              &:last-child {
                justify-content: center;
                h3 {
                  color: #81a332;
                  margin: 0;
                }
              }
            }
          }
        }
      }
    }
  }

  .StepRangeSlider {
    position: relative;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
      Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    width: 100%;
    padding-bottom: 30px;

    &__trackWrap {
      padding: 12px;
      border-radius: 20px;
      background-color: #e6f1cc;
      position: relative;
      z-index: 1;
      cursor: pointer;
    }

    &__wrap {
      position: relative;
      -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    }

    &__trackHead {
      position: relative;
      display: flex;
      transform: translateY(15px);
      padding: 0 12px;
      border-radius: 4px;
      overflow: hidden;
    }

    &__recommendMark {
      position: absolute;
      padding: 6px 8px;
      background: #fff;
      border: 2px solid #81a332;
      transform: translateX(-50%);
      border-radius: 8px;
      top: -100px;
      cursor: pointer;

      &:after {
        content: "";
        position: absolute;
        width: 2px;
        background: #81a332;
        left: 50%;
        transform: translate(-50%);
        bottom: -70px;
        top: 100%;
      }

      &:before {
        content: "";
        position: absolute;
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background: #81a332;
        bottom: -74px;
        left: 50%;
        transform: translate(-50%);
      }

      span {
        color: #81a332;
        font-size: 14px;
        font-weight: 500;
      }
    }

    &__currentPlanMark {
      position: absolute;
      padding: 2px 12px 2px 25px;
      top: -100px;
      background-image: url("data:image/svg+xml,%3Csvg width='91' height='29' viewBox='0 0 91 29' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.2977 3.17302C0.340114 1.85063 1.2849 0 2.91759 0H91V29H2.91759C1.28489 29 0.340115 27.1494 1.2977 25.827L8.65057 15.673C9.15739 14.9731 9.15739 14.0269 8.65057 13.327L1.2977 3.17302Z' fill='%23941759'/%3E%3C/svg%3E%0A");
      transform: translateX(-99%);
      cursor: pointer;

      span {
        color: #fff;
        font-size: 14px;
        font-weight: 500;
        line-height: 25px;
        text-transform: uppercase;
      }

      &:after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 20px;
        background: #941759;
        z-index: -1;
      }

      div {
        width: 20px;
      }

      &:after {
        content: "";
        position: absolute;
        width: 2px;
        background: #941759;
        bottom: -72px;
        top: 100%;
        right: 0;
      }

      &:before {
        content: "";
        position: absolute;
        width: 10px;
        height: 10px;
        border-radius: 100%;
        background: #941759;
        bottom: -81px;
        right: 0;
        transform: translateX(45%);
      }
    }

    &__labels {
      position: relative;
      margin: 5px 12px 0;
    }

    &__label {
      position: absolute;
      transform: translateX(-50%);
      @include adap-font(16, 12);
      font-weight: 500;

      &:first-child {
        transform: translateX(0);
        left: 0;
      }

      &:nth-child(2) {
        left: 11.7647%;
      }

      &:nth-child(3) {
        left: 23.5294%;
      }

      &:nth-child(4) {
        left: 38.2353%;
      }

      &:nth-child(5) {
        left: 50%;
      }

      &:nth-child(6) {
        left: 64.7059%;
      }

      &:nth-child(7) {
        left: 75%;
      }

      &:nth-child(8) {
        left: 89.7059%;
      }

      &:nth-child(9) {
        left: 100%;
      }
    }

    &__trackHeadItem {
      position: relative;
      padding: 10px 12px 110px;
      font-size: 24px;
      font-weight: 500;
      color: #79808d;

      &:first-child {
        width: 11.7647%;
      }
      &:nth-child(2) {
        width: 26.4706%;
      }
      &:last-child {
        width: 61.7647%;
      }

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 5px;
        bottom: 0;
      }

      &:nth-child(1):before {
        background: #afdd441f;
        left: -12px;
      }

      &:nth-child(2):before {
        background: linear-gradient(
          270deg,
          rgba(129, 163, 50, 0.12) 0%,
          rgba(174, 220, 68, 0.12) 100%
        );
      }

      &:nth-child(3) {
        margin-right: 0;
      }

      &:nth-child(3):before {
        right: -12px;
        background: linear-gradient(
          90deg,
          rgba(119, 145, 48, 0.12) 0%,
          rgba(211, 85, 152, 0.12) 34.9%,
          rgba(149, 24, 89, 0.12) 70.31%,
          rgba(230, 75, 60, 0.12) 100%
        );
      }
    }

    &__track {
      position: relative;
      display: flex;
      width: 100%;
      z-index: 1;
    }

    &__trackItem {
      padding: 0 5px;

      & > div {
        height: 10px;
        border-radius: 8px;
      }

      &:first-child {
        padding-left: 0;
      }

      &:last-child {
        padding-right: 0;
      }

      &:nth-child(1) {
        width: 11.7647%;
        & > div {
          background: #afdd44;
        }
      }

      &:nth-child(2) {
        width: 11.7647%;
        & > div {
          background: linear-gradient(to right, #afdd44, #81a332);
        }
      }

      &:nth-child(3) {
        width: 14.7059%;
        & > div {
          background: #81a332;
        }
      }

      &:nth-child(4) {
        width: 11.7647%;
        & > div {
          background: #81a332;
        }
      }

      &:nth-child(5) {
        width: 14.7059%;
        & > div {
          background: linear-gradient(to right, #81a332, #d35598);
        }
      }

      &:nth-child(6) {
        width: 10.2941%;
        & > div {
          background: #d35598;
        }
      }

      &:nth-child(7) {
        width: 14.7059%;
        & > div {
          background: linear-gradient(to right, #d35598, #941759);
        }
      }

      &:nth-child(8) {
        width: 10.2941%;
        & > div {
          background: #941759;
        }
      }
    }

    &__handle {
      position: absolute;
      left: 11.7647%;
      top: 50%;
      width: 32px;
      height: 32px;
      display: flex;
      align-items: center;
      justify-content: center;
      transform: translate(-50%, -50%);
      z-index: 2;
    }

    &__thumb {
      display: flex;
      justify-content: center;
      align-items: center;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      height: 32px;
      width: 32px;
      border: 4px solid #953459;
      border-radius: 50%;
      background: #f2f2f2;
      z-index: 2;
      /*-webkit-appearance: sliderthumb-horizontal;*/

      &:active {
        background-color: #590f36;
      }
    }

    &__tooltip {
      display: flex;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: -35px;
      padding: 3px 9px;
      font-size: 12px;
      line-height: normal;
      font-weight: 500;
      color: #333945;
      border: 2px solid #953459;
      border-radius: 8px;
      background: white;
    }

    @media screen and (max-width: 1024px) {
      display: grid;
      grid-template-columns: max-content max-content;
      column-gap: 10px;
      justify-content: center;
      height: 600px;
      padding: 0;
      margin: -2px 0;

      &__trackItem {
        height: 70px;
        width: 100% !important;
        padding: 2px 0;

        div {
          height: 100%;
        }
        &:first-child {
          padding-left: 0;
          margin-top: 0;
        }

        &:last-child {
          padding-right: 0;
        }

        &:nth-child(1) {
          height: 11.7647%;
          & > div {
            background: #afdd44;
          }
        }

        &:nth-child(2) {
          height: 11.7647%;
          & > div {
            background: linear-gradient(to bottom, #afdd44, #81a332);
          }
        }

        &:nth-child(3) {
          height: 14.7059%;
          & > div {
            background: #81a332;
          }
        }

        &:nth-child(4) {
          height: 11.7647%;
          & > div {
            background: #81a332;
          }
        }

        &:nth-child(5) {
          height: 14.7059%;
          & > div {
            background: linear-gradient(to bottom, #81a332, #d35598);
          }
        }

        &:nth-child(6) {
          height: 10.2941%;
          & > div {
            background: #d35598;
          }
        }

        &:nth-child(7) {
          height: 14.7059%;
          & > div {
            background: linear-gradient(to bottom, #d35598, #941759);
          }
        }

        &:nth-child(8) {
          height: 10.2941%;
          & > div {
            background: #941759;
          }
        }
      }

      &__tracks {
        display: grid;
        grid-template-columns: max-content max-content;
      }

      &__trackWrap {
        grid-column: 1;
        grid-row: 1;
        top: 0;
        height: 602px;
        width: 24px;
        padding: 7px;
      }

      &__wrap {
        height: 100%;
      }

      &__track {
        height: 100%;
        flex-wrap: wrap;
      }
      &__tooltip {
        top: 50%;
        left: 120%;
        transform: translateY(-50%);
      }
      &__handle {
        top: 11.7647%;
        left: 50%;
      }
      &__trackHead {
        flex-direction: column;
        transform: none;
        height: 602px;
        width: max-content;
        left: -15px;
        padding: 0;
      }
      &__trackHeadItem {
        font-size: 16px;
        text-align: end;
        margin-top: 4px;

        &:nth-child(1) {
          height: 77px;
          width: 114px;
          margin-top: 0;
          padding-bottom: 0;
          &:before {
            background: #afdd441f;
          }
        }

        &:nth-child(2) {
          height: 144px;
          width: 114px;
        }

        &:nth-child(3) {
          height: 373px;
          width: 114px;
        }

        &:before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          right: 5px;
          bottom: 0;
        }

        &:nth-child(1):before {
          left: 0;
          height: 77px;
          background: #afdd441f;
        }

        &:nth-child(2):before {
          background: linear-gradient(
            rgba(129, 163, 50, 0.12) 0%,
            rgba(174, 220, 68, 0.12) 100%
          );
        }

        &:nth-child(3) {
          margin-right: 0;
        }

        &:nth-child(3):before {
          right: 5px;
          background: linear-gradient(
            rgba(119, 145, 48, 0.12) 0%,
            rgba(211, 85, 152, 0.12) 34.9%,
            rgba(149, 24, 89, 0.12) 70.31%,
            rgba(230, 75, 60, 0.12) 100%
          );
        }
      }

      &__labels {
        grid-column: 1;
        grid-row: 1;
        top: 0;
        margin: 0;
        width: 40px;
      }

      &__label {
        transform: translateY(-50%);
        right: 0;
        &:nth-child(1) {
          top: 0;
          left: auto;
        }

        &:nth-child(2) {
          top: 11.7647%;
          left: auto;
        }

        &:nth-child(3) {
          top: 23.5294%;
          left: auto;
        }

        &:nth-child(4) {
          top: 38.2353%;
          left: auto;
        }

        &:nth-child(5) {
          top: 50%;
          left: auto;
        }

        &:nth-child(6) {
          top: 64.7059%;
          left: auto;
        }

        &:nth-child(7) {
          top: 75%;
          left: auto;
        }

        &:nth-child(8) {
          top: 89.7059%;
          left: auto;
        }

        &:nth-child(9) {
          top: 100%;
          left: auto;
        }
      }
    }
  }

  @media (max-width: 1024px) {
    .plan-content .plan-top {
      height: auto;
    }
  }

  @media (max-width: 350px) {
    .plan-content .plan-top {
      left: 20px;
    }
  }
}
